import React, { useEffect, useState } from "react";
import { NewPub } from "../publications/newPublications.jsx";
import Publication from "../publications/publication.jsx";
import Header from './Header.jsx'
import "regenerator-runtime";
import Aside from "./aside.jsx";
import Loading from "../partials/loading.jsx";
import { useUser } from "../../Contexts/user.jsx";
import Unlogged from "../partials/unlogged.jsx";
import {getPubs} from "../../modules/getPubs.jsx";
import {useTheme} from "../../Contexts/theme.jsx";

function Home({logindef}) {
    const {userState, token} = useUser();
    const {theme} = useTheme();
    const [pubs, setPubs] = useState();
    const [loading, setLoading] = useState(true);

    useEffect(async() => {
        setPubs(await getPubs());
    }, [userState]);

    useEffect(async() => {
        if(pubs){
            setLoading(false);
        }
        console.log(theme)
    }, [pubs])

    if(!loading){
            return(
                <div id="app-body" className={theme === 'light' ? 'bg-light' : 'bg-dark'}>
                    <React.StrictMode>
                    {screen.width > 600 ? <header>
                        <Header />
                    </header>: ''}
                    <div id="content">
                        <div id="content-pos">
                            <div id='center'>
                                <div className={`top-bar ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                                    {token ? 'Inicio': <div className="notranslate" id='title'>Luccagram</div>}
                                </div>
                                <div className="top-bar-space" />
                                <div id="home-body">
                                    <NewPub setPubs={setPubs} />
                                    <Publication pubs={pubs}/>
                                </div>
                                <div id="bottom-bar-space" />
                            </div>
                        </div>
                    </div>
                    <footer>
                        <Aside />
                    </footer>
                    {screen.width < 600 && token ? <header>
                        <Header />
                    </header>: ''}
                    {!token ? <Unlogged logindef={logindef} />:''}
                    </React.StrictMode>
                </div>
            )
    }else{
        return <Loading />
    }
        
}

export default Home