import React from 'react'
import { useUser } from '../../Contexts/user.jsx';
import { useSocket } from '../../Contexts/socket.jsx';
import { useEffect } from 'react';
import {useTheme} from "../../Contexts/theme.jsx";

function AsideL() {
    const {theme} = useTheme();
    const {socket} = useSocket();
    const {userState, token} = useUser();

    const logout = () => {
        socket.emit('disconnected', userState.user)
        document.cookie.split(';').map(c => {
            if(c.indexOf('auth-token') !== -1){
                document.cookie = 'auth-token=;max-age=0;path=/';
                localStorage.removeItem('user')
            }
        });
        window.location.pathname='/'
    }

    useEffect(() => {
        if(screen.width < 600){
            window.addEventListener('scroll', (e) => {
                document.getElementById('header-pos').style.bottom = 0;
            })
        }
    }, [])

    if(screen.width >= 600){
    return (
        <div id='header-pos' className={`${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
            <div id="header">
                <div id="title">
                    <a className={`notranslate a-normalize ${theme === 'light' ? 'bg-light' : 'bg-dark'}`} href="/">Luccagram</a>
                </div>
                {token ?
                <div id="header-nav">
                    <div id="nav-lspace"></div>
                    <div id="nav-btns">
                        <a href='/' id="Home-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                        <span className={`material-icons notranslate gicon header-icon ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                            home
                        </span>
                            Inicio
                        </a>
                        <a href={userState ? `/user/${userState?.user}`: ''} id="Profile-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                            <span className={`material-icons notranslate gicon header-icon ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                                person
                            </span>
                            Perfil
                        </a>
                        <a href='/notifications' id="notification-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                            <span className={`material-icons notranslate gicon header-icon ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                                    notifications
                                </span>
                                <div id="new-notification-div">
                                    {userState?.newNotis > 0 ? <span id="new-notification">{userState?.newNotis}</span>:''}
                                </div>
                            Notificaciones
                        </a>
                        <a href='/tema' id="theme-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                        <span className={`material-icons notranslate gicon header-icon ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>brush</span>
                            Tema
                        </a>
                        <a onClick={logout} id="logout-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                        <span className={`material-icons notranslate gicon header-icon ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>logout</span>
                            Cerrar Sesin
                        </a>
                    </div>
                </div>:''}

                </div>
                
                {token ? <div id="account">
                    <div id="account-btn">
                    <img id="account-pic" src={userState ? userState?.profilePic : ''} alt={userState?.user} />
                    <div className='notranslate' id="account-user">
                        {userState?.user}
                    </div>
                    </div>
                </div>:''}
        </div>
        
    )}else 
    if(token){
        return(
            <>
            <div id='header-pos'>
                <a href='/' id="Home-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                    <span className="material-icons notranslate gicon header-icon">
                        home
                    </span>
                </a>
                <a href='/search' id="search-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                    <span className="material-icons notranslate gicon header-icon">
                        search
                    </span>
                </a>
                <a href='/notifications' id="noti-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                    <span className="material-icons notranslate gicon header-icon">
                        notifications
                    </span>
                </a>
                <a href='/chat' id="chat-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                    <span className="material-icons notranslate gicon header-icon">
                        mail
                    </span>
                </a>
                <a href={'/user/'+userState?.user} id="chat-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                    <img id="Profile-btn" className={`a-normalize header-btn ${theme === 'light' ? 'bg-light' : 'bg-dark'}`} src={userState?.profilePic} />
                </a>
            </div>
            </>
        )
    }else return ''
}

export default AsideL