import React, { useEffect, useState } from "react";
import { useUser } from "../../Contexts/user.jsx";
import ErrorMsg from './error.jsx';
import { useTheme } from "../../Contexts/theme.jsx";

const Unlogged = ({logindef}) => {
    const {theme} = useTheme();
    const [active, setActive] = useState(logindef || false);
    const [form, setForm] = useState('login');

    useEffect(() => {
        window.addEventListener('click', (e) => {
            if(e.target.getAttribute('id') === 'login-form-bg'){
                setActive(false);
            }
        })
    }, [])

    if(!active){
    return(
        <div id='login-bar'>
            <span className="notranslate" id="login-bar-title">
                Luccagram
            </span>
            <div id="login-bar-btns">
                <span onClick={() => {setActive(true);setForm('login')}} id="login-btn">
                    Iniciar Sesin
                </span>
                <span onClick={() => {setActive(true);setForm('signup')}} id="signup-btn">
                    Registrarse
                </span>
            </div>
        </div>
    )}else{
        return (
            <div id='login-form-bg'>
                <Form form={form} setActive={setActive}/>
            </div>
        )
    }
}

const Form = ({form, setActive}) => {
    const {userState} = useUser();
    const [errors, setErrors] = useState([])
    const [forgetPass, setForgetPass] = useState(true);

    useEffect(() => {
        if(userState && userState.errors) setErrors(userState.errors);
    }, [userState]);

    async function logging(e){
        e.preventDefault()

        const remember = document.getElementsByName('remember')[0].value;
        const user = document.getElementsByName('user')[0].value;
        const password = document.getElementsByName('password')[0].value;

        const res = await fetch(`/api/login`,{
            method: 'POST',
            body: JSON.stringify({
                user,
                password
            }),
            headers: {
                'content-type': 'application/json'
            }
        })
        const data = await res.json();
            if (data.errors){
                setErrors(data.errors)
            }else{
                if(remember.checked){
                    document.cookie += "auth-token=" + data.token + ";max-age" + 60*60*24*365*100;
                }else{
                    document.cookie += "auth-token=" + data.token;
                }
                window.location.reload();
            }
    }

    const signUp = async (e) => {
        e.preventDefault();
        
        setErrors([]);
        
        const user = document.getElementsByName('user')[0].value;
        const password = document.getElementsByName('password')[0].value;
        const confirmPassword = document.getElementsByName('confirm-password')[0].value;
        const email = document.getElementsByName('email')[0].value;

        const res = await fetch(`/api/signup`,{
            method: 'POST',
            body: JSON.stringify({
                user, 
                password,
                confirmPassword,
                email
            }),
            headers: {
                'content-type': 'application/json'
            }
        });
        const data = await res.json();

        if(data.errors){
            setErrors(data.errors);
        }else{
            document.cookie = "auth-token=" + data.token + ";SameSite=None";
            window.location.href = '/';
        }

    }

    return (
        <div id="login-form-div">
            {errors ? <ErrorMsg errors={errors} /> : '' }
                {screen.width >= 600 ? <div className="notranslate" id="title">Luccagram</div>:
                <div className={`top-bar ${theme === 'light' ? 'bg-light' : 'bg-dark'}`}>
                    <div className="notranslate" id="title">Luccagram</div>
                    <span onClick={() => {setActive(false)}} id="close-btn" className="material-icons notranslate">close</span>
                </div>
                }
            {form === 'login' ? forgetPass ?
                <div id="login-form">
                    <input type="text" placeholder="usuario" name="user" id="username" className="form-input" autoFocus />
                    <input type="password" placeholder="contrasea" name="password" id="password" className="form-input" />
                    <input onClick={logging} type="submit" value="Ingresar" id="submit" className="form-input" />
                    <span id="remember-div" className="form-input" >
                        <input type="checkbox" name="remember" id="remember" />
                        <label htmlFor="remember"> Mantener sesin iniciada </label>
                    </span>
                    <span id="forgetPass" onClick={() => {setForgetPass(!forgetPass)}} className="a-normalize">Olvidaste tu contrasea?</span>
                </div>: <ForgetPass />
                :

            <div id="login-form">
                <input type="text" placeholder="usuario" name="user" id="username" className="form-input" autoFocus />
                <input type="password" placeholder="contrasea" name="password" id="password" className="form-input" />
                <input type="password" placeholder="confirmar contrasea" name="confirm-password" id="confirm-password" className="form-input" />
                <input type="text" placeholder="e-mail" name="email" id="email" className="form-input" />
                <input onClick={signUp} type="submit" value="Ingresar" id="submit" className="form-input" />
            </div>
            }
        </div>
    )
}

const ForgetPass = () => {
    const [success, setSuccess] = useState(false);
    const [errors, setErrors] = useState([]);
    const [email, setEmail] = useState([]);
    const [changePassword, setChangePassword] = useState(false);
    const [code, setCode] = useState(null)

    const recoverPass = async(e) => {
        e.preventDefault();

        const email = document.getElementsByName('recover-pass')[0].value;

        const res = await fetch('/api/recoverPass', {
            method: 'POST',
            headers: {
                'content-type': 'application/json',
            },
            body: JSON.stringify({email})
        });
        const data = await res.json();
        if(!data.errors){
            alert('listo pa')
            setSuccess(true)
            setEmail(email);
        }else{
            setErrors(data.errors)
        }
    }

    const confirmCode = async(e) => {
        e.preventDefault();
        const code = document.getElementById('code').value;
        const res = await fetch('/api/confirmCode', {
            method: 'post',
            headers: {
                'content-type': 'application/json',
            },
            body: JSON.stringify({code, email}),
        });
        const data = await res.json();
        if(!data.errors){
            setCode(code)
            setChangePassword(true)
        }else{
            setErrors(data.errors)
        }
    }

    const confirmPassword = async(e) => {
        e.preventDefault();
        const password = document.getElementById('password').value;
        const confirmPassword = document.getElementById('confirm-password').value;
        const res = await fetch('/api/changePassword', {
            method: 'POST',
            headers: {
                'content-type': 'application/json',
            },
            body: JSON.stringify({
                password,
                confirmPassword,
                email,
                code
            })
        });
        const data = await res.json();
        if(!data.errors){
            window.location.href = '/';
        }else{
            setErrors(data.errors)
        }
    }

    return (
            <div id="login-form">
                {errors ? <ErrorMsg errors={errors} /> : '' }
                {!success ? 
                    <>
                        <span id="recover-text">Para recuperarla necesitamos tu email</span>
                        <input type="text" name="recover-pass" className="form-input" placeholder="email" />
                        <input onClick={recoverPass} type="submit" id="submit" className="form-input" value="Enviar" />
                    </>: !changePassword ?
                    <>
                        <span id="success-text">Se envi un correo a {email}, revisa tu casilla de correo y escribe el codigo que se te envio abajo</span>
                        <input type="text" defaultValue={''} id="code" name="recover-pass" className="form-input" placeholder="Cdigo" autoFocus/>
                        <input onClick={confirmCode} type="submit" id="submit" className="form-input" value="Enviar" />
                    </>:
                    <>
                        <input type="password" id="password" className="form-input" placeholder="contrasea nueva" />
                        <input type="password" id="confirm-password" className="form-input" placeholder="confirmar contrasea" />
                        <input onClick={confirmPassword} type="submit" id="submit" className="form-input" value="Enviar" />
                    </>
                }
            </div>
    )
} 

export default Unlogged;